<template>
  <div class="mask-wrap">
    <img class="close-img" 
    src="./imgs/icon-close.png" @click="close">
    <div class="tip-text" v-if="!shareImg"></div>
    <div class="tip-text" v-else></div>
    <template>
      <div class="share-wrap img-box" v-if="shareImg">
        <img class="share-img" :src="shareImg" alt="" srcset="">
      </div>
      <div class="main-wrap" v-else ref="shareImg">
        <div class="share-wrap">
        <div class="head-img-box">
          <!---->
          <img :src="headImg" alt="" class="head-img">
          <!---->
          <div class="nick-name flex-column">
            <span class="one-txt-cut">{{type === 'week' ? '':''}}</span>
            <span>{{`${dateRange.startDate.replace(/\-/g, '.')} - ${dateRange.endDate.replace(/\-/g, '.')}`}}</span>
          </div>
        </div>

        <div class="statistics-time-wrap">
          <div class="time-item">
            <span class="time">{{punchinData.punchinMorning}}</span>
            <span class="time-desc"></span>
          </div>
          <div class="time-item">
            <span class="time">{{punchinData.punchinNight}}</span>
            <span class="time-desc"></span>
          </div>
          <div class="time-item">
            <span class="time">{{punchinData.punchinAvg}}</span>
            <span class="time-desc"></span>
          </div>
        </div>

        <div class="statistics-chart-wrap">
          <div class="top-box">
            <span class="chart-title">{{type === 'week' ? '' : ''}}</span>
          </div>

          <div class="chart-wrap">
            <img class="chart-img" :src="chartImgUrl" alt="">
          </div>

          <div class="time-days-box">
            <div class="days-item">
              <span class="days">{{continueData.continueTime}}</span>
              <span class="desc"></span>
            </div>
            <div class="days-item">
              <span class="days">{{continueData.allTime}}</span>
              <span class="desc">{{type==='week'? '':''}}</span>
            </div>
          </div>
        </div>

        <div class="qr-img-box">
          <img src="../clock/imgs/qr-code.jpg" alt="" srcset="">
        </div>
        <div class="qr-desc"></div>
      </div>
      </div>
      
    </template>
  </div>
</template>
<script>
import html2canvas from 'html2canvas';
import {mapState, mapGetters, mapMutations} from 'vuex';
import LineChart from './components/LineChart.vue'
import TimeRangeChange from "./components/TimeRangeChange";
export default {
  name: 'sharePage',
  components: {LineChart},
  props: {
    punchinData: {
      type: Object,
      default() {
        return {
          punchinMorning: '--',
          punchinNight: '--',
          punchinAvg: '--',
        }
      }
    },
    continueData: {
      type: Object,
      default() {
        return {
          allTime: 0,
          continueTime: 0
        }
      }
    },
    dateRange: {
      type: Object,
      default() {
        return {}
      }
    },
    chartImgUrl: {
      type: String,
      default: ''
    },
    headImgBase64: {
      type: String,
      default: ''
    },
    type: {
      type: String,
      default: 'week'
    },
    visable: Boolean,
  },
  data() {
    return {
      shareImg: '',
      headImg: this.headImgBase64 || require('./imgs/head-img.png')
    }
  },
  computed: {
  ...mapState([
      'pinchinInfo'
    ]),
  ...mapGetters([
      'hasPunchin'
    ])
  },
  methods: {
    close() {
      this.$emit('update:visable', false)
    },
  },
  mounted() {
      this.timer = setTimeout(() => {
        const dom = this.$refs.shareImg
        html2canvas(dom,{useCORS: true, backgroundColor: 'rgba(255,255,255,0)', scale: 3 }).then(canvas => {
          this.shareImg = canvas.toDataURL("image/jpg")
        })
        
      },10)
    },
  destroyed() {
    clearTimeout(this.timer)
  }
}
</script>
<style lang="scss" scoped>
.mask-wrap {
  width: 100%;
  height: 100%;
  background: rgba(0,0,0, 0.8);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  .close-img {
    height: 42px;
    width: 42px;
    position: absolute;
    top: 30px;
    right: 50px;
  }
}
.tip-text {
  height: 32px;
  font-size: 30px;
  text-align: center;
  color: rgba(255, 255, 255, 0.8);
  margin: 1px 0 45px;
}
@media screen and (max-height: 600px) {
  .tip-text {
    margin: 10px 0 25px;
  }
}
.main-wrap {
  height: 990px;
  width: 552px;
  // border-radius: 10px;
  background-color: rgba(255, 255, 255, 0);
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}
.share-wrap {
  height: 994px;
  width: 556px;
  background: url("./imgs/bg-share.jpg") no-repeat;
  background-size: 558px 994px;
  background-color: rgba(255, 255, 255, 0);
  box-sizing: border-box;
  padding: 0 68px;
  border: none;
  position: relative;
  &.img-box {
    border-radius: 10px;
    overflow: hidden;
  }
  .share-img {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
  }
  .head-img-box {
    width: 100%;
    height: 150px;
    display: flex;
    align-items: center;
    .head-img {
      width: 86px;
      height: 86px;
      border-radius: 50%;
      box-shadow: 0px 14px 32px 0px rgba(117, 20, 16, 0.21);
      background: #fff;
      margin-left: 30px;
    }

    .nick-name {
      height: 86px;
      margin-left: 20px;
      align-items: flex-start;
      justify-content: space-around;
      span {
        // width: 320px;
        &:nth-of-type(1) {
          font-size: 32px;
          color: #fff;
          // font-weight: bold;v
        }
        &:nth-of-type(2) {
          font-size: 24px;
          color: rgba(255, 255, 255, 0.8);
        }
      }
    }
  }

  .statistics-time-wrap {
    border-radius: 10px;
    width: 100%;
    height: 92px;
    background: #fff;
    box-sizing: border-box;
    padding: 16px 0;
    display: flex;
    justify-content: space-between;
    .time-item {
      width: 230px;
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
      span {
        padding: 0;
      }
      .time {
        font-size: 26px;
        color: #e93325;
        font-weight: bold;
      }
      .time-desc {
        font-size: 18px;
        color: #666;
      }
    }
  }

  .statistics-chart-wrap {
    width: 100%;
    height: 540px;
    background: #fff;
    border-radius: 10px;
    margin-top: 14px;
    padding-top: 10px;
    box-sizing: border-box;
    position: relative;
    .top-box {
      width: 100%;
      height: 30px;
      box-sizing: border-box;
      padding: 0 20px;
      position: absolute;
      top: 28px;
      left: 0;
      z-index: 2;
      display: flex;
      justify-content: space-between;
      align-items: flex-end;
      .chart-title {
        font-size: 26px;
        color: #333;
        font-weight: bold;
      }
      .share-btn-box {
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        img {
          width: 32px;
          height: 32px;
        }
        span {
          height: 26px;
          font-size: 18px
        }
      }
    }
    .chart-wrap {
      width: 100%;
      height: 460px;
      margin-left: 16px;
      .chart-img {
        width: 100%;
        height: 104%;
      }
    }

    .time-days-box {
      height: 80px;
      width: 100%;
      display: flex;
      align-items: center;
      position: absolute;
      left: 0;
      bottom: 0;
      .days-item {
        flex: 1;
        height: 60px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        & + .days-item {
          border-left: 1px solid #cfcfcf;
        }
        .days {
          font-size: 26px;
          font-weight: bold;
          color: #333;
        }
        .desc {
          font-size: 18px;
          color: #666;
        }
      }
    }
  }

  .qr-img-box {
    width: 124px;
    height: 124px;
    margin: 30px auto 0;
    padding: 7px;
    box-sizing: border-box;
    background-color: #fff;
    img {
      height: 100%;
      width: 100%;
    }
  }
  .qr-desc {
    width: 100%;
    height: 24px;
    text-align: center;
    color: #fff;
    font-size: 22px;
    margin-top: 6px;
  }
}
</style>