<template>
  <div class="time-range-change">
        <div class="btn" @click="changeRange('prev')">
          <img src="../imgs/icon-arrow-left.png" class="arrow prev"/>
        </div>
        <span class="time-range">{{timeRange.startDate | formatDate}} - {{timeRange.endDate | formatDate}}</span>
        <div class="btn" @click="changeRange('next')">
          <img src="../imgs/icon-arrow-right.png" class="arrow next"/>
        </div>    
      </div>
</template>

<script>
const DAYRANGE = 60 * 60 * 24 * 1000
export default {
  name: 'TimeRangeChange',
  props: {
    type: {
      type: String,
      default: 'week'
    },
    value: {
      type: Object,
      default() {
        return {}
      }
    }
  },
  data() {
    return {
      diff: 0,   // 
    }
  },
  filters: {
    formatDate(val) {
      return val.replace(/\-/g, '.')
    }
  },
  computed: {
    timeRange() {
      let timeRange = {}
      if(this.type === 'week') {
        timeRange = this.getWeekTimeRange()
      } else {
        timeRange = this.getMonthTimeRange()
      }
      this.$emit('input', timeRange)
      this.$emit('change', timeRange)
      return timeRange
    }
  },
  methods: {
    changeRange(flag) {
      if(flag === 'prev') {
        this.diff--
      } else {
        this.diff >= 0 ? this.diff : this.diff++
      }
    },
    getDateStr(timestamp) {
      const targetTime = new Date(timestamp)
      return `${targetTime.getFullYear()}-${this.add0(targetTime.getMonth()+1)}-${this.add0(targetTime.getDate())}`
    },
    add0(val) {
      return val < 10 ? '0' + val : val
    },
    getWeekTimeRange() {
      const interval = DAYRANGE * 7
      const nowTime = +new Date()
      const targetDate = new Date(nowTime + interval * this.diff)
      let targetDay = targetDate.getDay()
      targetDay = targetDay ? targetDay : 7
      const startDate = this.getDateStr(targetDate.getTime() - DAYRANGE * (targetDay-1))
      const endDate = this.getDateStr(targetDate.getTime() + DAYRANGE*(7-targetDay))
      return {startDate, endDate}
    },
    getMonthTimeRange() {
      const targetDate = new Date().setMonth(new Date().getMonth() + this.diff)
      const nextMonth = new Date().setMonth(new Date().getMonth() + (this.diff + 1))
      
      const startDate = this.getDateStr(new Date(targetDate).setDate(1))
      const endDate = this.getDateStr(new Date(nextMonth).setDate(0))
      return {startDate, endDate}
    }
  },
}
</script>

<style lang="scss" scoped>
.time-range-change {
    height: 50px;
    width: 500px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 26px;
    color: #fff;
    .btn {
      height: 100%;
      width: 70px;
      display: flex;
      align-items: center;
      justify-content: center;
    }
    .arrow {
      height: 30px;
      width: 32px;
      border-radius: 50%;
    }
  }
</style>