//  waitfn
export function debounceFn(fn, wait=500) {
  let timeout = null;
  return function(...rest) {
    clearTimeout(timeout);
    timeout = setTimeout(() => {
      fn.apply(this,rest);
      timeout = null;
    }, wait);
  }
}
// 
export function formatNum (num) {
  if (!num) return 0
  num = (num + '').split('.')

  const reg=/\d{1,3}(?=(\d{3})+$)/g;
  return num[0].replace(reg, '$&,') + (num[1] ? '.' + num[1] : '') ;
}

// 
export function throttleFn(fn, delay=1000) {
  let timer = null;
  return function(...rest) {
    if (timer) return
    timer = setTimeout(() => {
        timer = null;
      }, delay);
    fn.apply(this,rest);
  }
}