<template>
  <div class="statistics-wrap">
    <div class="top-bg"></div>
    <div class="top-tab-wrap">
      <div class="tab-btn-box flex-center">
        <span class="tab-btn left flex-center" 
              :class="{'active': type === 'week'}"
              @click="changeType('week')">
              
        </span>
        <span class="tab-btn right flex-center" 
              :class="{'active': type === 'month'}"
              @click="changeType('month')">
              
        </span>
      </div>

      <TimeRangeChange v-show="type==='week'" v-model="weekTimeRange" type="week" key="1"/>
      <TimeRangeChange v-show="type==='month'" v-model="monthTimeRange" type="month" key="2"/>
    
    </div>
    
    <div class="statistics-time-wrap">
      <div class="time-item">
        <span class="time">{{punchinData.punchinMorning}}</span>
        <span class="time-desc"></span>
      </div>
      <div class="time-item">
        <span class="time">{{punchinData.punchinNight}}</span>
        <span class="time-desc"></span>
      </div>
      <div class="time-item">
        <span class="time">{{punchinData.punchinAvg}}</span>
        <span class="time-desc"></span>
      </div>
    </div>


    <div class="statistics-chart-wrap">
      <div class="top-box">
        <span class="chart-title">{{type === 'week' ? '' : ''}}</span>
        <div class="share-btn-box" @click="toShare" v-if="showShareBtn">
          <img src="./imgs/icon-share-btn.png" alt="">
          <span></span>
        </div>
      </div>

      <LineChart 
                 class="chart-wrap" 
                 :chartData="chartData" 
                 :type="type"
                 v-model="chartImgUrl"
                 :key="weekTimeRange.startDate"/>

      <div class="time-days-box">
        <div class="days-item">
          <span class="days">{{continueData.continueTime}}</span>
          <span class="desc"></span>
        </div>
        <div class="days-item">
          <span class="days">{{continueData.allTime}}</span>
          <span class="desc">{{type==='week'? '':''}}</span>
        </div>
      </div>
    </div>

    <sharePage v-if="sharePageVisable" 
               class="share-box"
               :type="type"
               :punchinData="punchinData"
               :continueData="continueData"
               :chartImgUrl="chartImgUrl"
               :headImgBase64="headImgBase64"
               :dateRange="dateRange"
               :visable.sync="sharePageVisable"/>
      <img v-if="!headImgBase64 && !sharePageVisable" :src="$store.state.userInfo.headImg" alt="" class="pre-img" ref="preImg">
  </div>
</template>

<script>
import TimeRangeChange from './components/TimeRangeChange'
import LineChart from './components/LineChart.vue'
import { clog, punchinStatistics } from '../../api/punchin'
import { parseTime, getBase64Image } from '../../utils/tools'
import { debounceFn } from '../../utils/utilsFn'
import sharePage from './sharePage.vue'
import { Toast, Indicator } from "mint-ui";
import { resolve } from 'path'
import { reject } from 'q'
import html2canvas from 'html2canvas';
export default {
  name: 'statistics',
  components: {LineChart, TimeRangeChange, sharePage},
  data() {
    return {
      type: 'week',
      weekTimeRange: {},
      monthTimeRange: {},
      chartData: [],
      sharePageVisable: false,
      punchinData: {
        punchinMorning: '',
        punchinNight: '',
        punchinAvg: '',
      },
      continueData: {
        allTime: 0,
        continueTime: 0
      },
      chartImgUrl: '',
      headImgBase64: '',
      showShareBtn: false
    }
  },
  computed: {
    dateRange() {
      return this.type === 'week' ? this.weekTimeRange : this.monthTimeRange
    }
  },
  methods: {
    toShare() {
      this.sharePageVisable = true
      try{
        clog("", [
          "vjf-h5-log",
          "transTime",
          "openid",
          "",
          location.href.split("?")[0],
          "",
          ""
        ]);
        _hmt.push(["_trackEvent", "click", '', ""]);
      } catch(e) {
        console.log('');        
      }
    },
    changeType(val) {
      this.type = val
    },
    handleChartChange() {
      Indicator.open('')
      this.updataPunchinData()
    },
    updataPunchinData: debounceFn(function() {
      if(!this.hasInit) return Promise.resolve()
      return this.queryPunchinData().then(res => {
        Indicator.close()
      })
    }),
    queryPunchinData() {
      const rangeTime = this.type === 'week' ? this.weekTimeRange : this.monthTimeRange 
      const data = {
        type: this.type === 'week' ? 1 : 2,	//12
        beginPunchinTime: rangeTime.startDate,
        endPunchinTime: rangeTime.endDate,
      }
      
      return punchinStatistics(data).then(res => {
        if(res.result.code === '0' && res.result.businessCode === '0') {
          this.setpunchinData(res, rangeTime)
        }
      })
    },
    // , data
    setpunchinData(resData, rangeTime) {
      if(!resData.reply.length) {
        Toast({
          message: '',
          position: 'center',
          duration: 1500
        });
        this.showShareBtn = false
      } else {
        this.showShareBtn = true
      }
      const { 
          punchinMorning = '--', 
          punchinNight = '--', 
          punchinAvg = '--'
        } = resData.reply[0] || {}
        this.punchinData = {
          punchinMorning,
          punchinNight,
          punchinAvg,
        }
        this.chartData = this.handleChartData(resData.reply , rangeTime, this.type)
        this.continueData.allTime = resData.reply.length
        this.continueData.continueTime = this.handleContinueData(this.chartData)
        this.hasInit = true
    },
    // 
    handleChartData(originData, rangeData, type) {
      const range = type === 'week' ? 7 : new Date(rangeData.endDate).getDate() * 1
      const originObj = {}
      for (const item of originData) {
        const punchinTime = item.punchinTime.split(' ')
        originObj[punchinTime[0]] = +punchinTime[1].split(':')[0] + punchinTime[1].split(':')[1] / 60
      }
      const dayRange = 60 * 60 * 24 * 1000
      const startTimeStamp = +new Date(rangeData.startDate)
      const chartData = []
      for(let i=0; i<range; i++) {
        const curDate = parseTime(startTimeStamp+dayRange*i, '{y}-{m}-{d}')
        chartData.push(originObj[curDate] || null)
      }
      return chartData  
    },
    // 
    handleContinueData(chartData) {
      let continueTime = 0
      let maxContinue = 0
      chartData.map(item => {
        if(item) {
          continueTime++
          if(continueTime > maxContinue) maxContinue = continueTime
        } else {
          continueTime = 0
        }
      })

      return maxContinue
    },
  },
  mounted() {
    this.queryPunchinData()

    //  (, )
    const img = this.$refs.preImg
    html2canvas(img,{useCORS: true, backgroundColor: 'rgba(255,255,255,0)', scale: 3 })
      .then(canvas => {
          const dataURL = canvas.toDataURL("image/jpg")
          this.headImgBase64 = dataURL
      })
  },
  watch: {
    weekTimeRange: 'handleChartChange',
    type: 'handleChartChange',
    monthTimeRange: 'handleChartChange',
  }
}
</script>
<style>
.mint-indicator-wrapper, .mint-indicator-mask {
  z-index: 99;
}
</style>
<style lang="scss" scoped>
.statistics-wrap {
  position: relative;
  width: 750px;
  min-height: 1200px;
  box-sizing: border-box;
  padding: 0 20px;
  overflow: hidden;
  z-index: 1;
  .top-bg {
    width: 1166px;
    height: 362px;
    background-image: linear-gradient( -90deg, rgb(216,42,78) 0%, rgb(216,42,78) 18%, rgb(232,44,31) 88%, rgb(232,44,31) 100%);
    border-bottom-left-radius: 55%;
    border-bottom-right-radius: 55%;
    position: absolute;
    left: -212px;
    top: -90px;
    z-index: -1;
  }

  .top-tab-wrap {
    height: 178px;
    width: 100%;
    overflow: hidden;
    .tab-btn-box {
      width: 246px;
      height: 40px;
      border: 1px solid #fff;
      border-radius: 6px;
      color: #333;
      font-size: 26px;
      margin: 30px auto 0;
      flex-wrap: nowrap;
      // overflow: hidden;
      .tab-btn {
        flex: 1;
        height: 102%;
        background: none;
        color: #fff;
        &.active {
          background: #fff;
          color: #333;
          &.left {
            border-top-left-radius: 3px;
            border-bottom-left-radius: 3px;
          }
          &.right {
            border-top-right-radius: 3px;
            border-bottom-right-radius: 3px;
          }
        }
      }
    }
  }

  .time-range-change {
    margin: 25px auto;
  }

  .statistics-time-wrap {
    border-radius: 10px;
    width: 710px;
    height: 142px;
    background: #fff;
    box-sizing: border-box;
    padding: 30px 0;
    display: flex;
    justify-content: space-between;
    .time-item {
      width: 230px;
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
      span {
        padding: 0;
      }
      .time {
        font-size: 36px;
        color: #e93325;
        font-weight: bold;
      }
      .time-desc {
        font-size: 24px;
        color: #666;
      }
    }
}


  .statistics-chart-wrap {
    width: 100%;
    height: 840px;
    background: #fff;
    border-radius: 10px;
    margin-top: 20px;
    padding-top: 20px;
    box-sizing: border-box;
    position: relative;
    .top-box {
      width: 100%;
      height: 62px;
      box-sizing: border-box;
      padding: 0 30px;
      position: absolute;
      top: 28px;
      left: 0;
      z-index: 2;
      display: flex;
      justify-content: space-between;
      align-items: flex-end;
      .chart-title {
        font-size: 34px;
        color: #333;
        font-weight: bold;
      }
      .share-btn-box {    
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        img {
          width: 32px;
          height: 32px;
        }
        span {
          height: 26px;
          font-size: 24px
        }
    }
    }
    .chart-wrap {
      width: 100%;
      height: 740px;
      margin-left: 28px;
    }

    .time-days-box {
      height: 120px;
      width: 100%;
      display: flex;
      align-items: center;
      position: absolute;
      left: 0;
      bottom: 0;
      .days-item {
        flex: 1;
        height: 80px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        & + .days-item {
          border-left: 1px solid #cfcfcf;
        }
        .days {
          font-size: 34px;
          font-weight: bold;
          color: #333;
        }   
        .desc {
          font-size: 24px;
          color: #666;
        }
      }
    }
  }
  
}

.share-box {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2;
}

.pre-img {
  height: 132px;
  width: 132px;
  position: relative;
  z-index: -10;
  top: -200px;
  left: 50px;
}
</style>