<template>
  <div class="chart-wrap" ref="chart"></div>
</template>

<script>
import { log } from 'util'
const baseOptions = {
            title: {
                text: ''
            },
            grid: {
              left: 40,
              // top: 30
              // width: 280
            },
            animation: false,
            xAxis: [{
                type: 'category',
                boundaryGap: true,
                axisTick: {
                    show: false
                },
                axisLine: {
                  lineStyle: {
                    color: '#cfcfcf'
                  }
                },
                axisLabel: {
                  color: '#666',
                  interval: false
                },
                data: []
            }],
            yAxis: [{
                type: 'value',
                min: 4,
                max: 12,
                splitNumber: 8,
                axisLine: {
                  show: false
                },
                axisTick: {
                  show: false
                },
                splitLine: {
                  lineStyle: {
                    color: '#dfdfdf',
                    width: 1 
                  }
                },
                axisLabel: {
                  color: '#666',
                  formatter(value) {
                      return value + ':00'
                  }
                }
            }],
            series: [{
                name: '',
                type: 'line',
                stack: '',
                smooth: true,
                smoothMonotone: 'x',
                label: {
                    normal: {
                        show: true,
                        // position: 'bottom',
                        formatter: function(params) {
                            var valueInt = Math.floor(params.value)
                            var valueDot = ((params.value % 1) * 60).toFixed(0)
                            return (valueInt >= 10 ? valueInt : `0${valueInt}`) + ':' + (valueDot > 10 ? valueDot :
                            `0${valueDot}`)
                        }
                    },
                },
                // areaStyle: {
                //   color: 'none'
                // },
                areaStyle: {
                  color: {
                    type: 'linear',
                    x: 0,
                    y: 0,
                    x2: 0,
                    y2: 1,
                    colorStops: [
                      {
                        offset: 0, color: '#ffd4d0'
                      },
                      {
                        offset: 0.6, color: 'rgba(255, 208, 208, 0.1)' // 100% 
                      },
                      {
                        offset: 1, color: 'rgba(255,255,255,0)' // 100% 
                      }
                    ],
                    global: false //  false
                  }
                },
                lineStyle: {
                  color: '#ffd4d0',
                  width: 1
                },
                itemStyle: {
                  color: '#e93325',
                  borderColor: '#e93325',
                  borderWidth: 1
                },
                data: []
            }]
        }

export default {
  name: 'line-chart',
  props: {
    chartData: {
      type: Array,
      default() {
        return []
      }
    },
    type: {
      type: String,
      default: 'week'
    }
  },
  data() {
    return{
      imgUrl: ''
    }
  },
  methods: {
    setOption() {
      this.options = baseOptions
      if(!this.chartData.length) return
      {
        const seriesData = []
        const validData = this.chartData.filter(item => item)
        const min = Math.min(...validData)
        const max = Math.max(...validData)
        const copyData = this.chartData.slice().reverse()
        let hasmax = false
        let hasmin = false
        copyData.map((item, index) => {
          let itemData = {
                value: item,
                label: { show: false},
                // itemStyle: {opacity: this.type === 'week' ? 1 : 0 },
                itemStyle: {opacity: this.type === 'week' ? 1 : 0 }
              }
          if(!(copyData[index-1] || copyData[index+1])) {
            itemData.itemStyle = {opacity: 0.5}
          }
          if(item === min && !hasmin) {
            hasmin = true
            itemData = {
              value: item,
              label: {
                position: 'bottom'
              }
            }
          } else if(item === max && !hasmax) {
            hasmax = true
            itemData = {
              value: item,
              label: {
                position: 'top'
              }
            }
          }
          seriesData.push(itemData)
        })      
        this.options.series[0].data = seriesData.reverse()
      }
      if(this.type === 'week') {
        this.options.xAxis[0].data = ['', '', '', '', '', '', '']
        
      } else {

        const xData = []
        const length = this.chartData.length
        this.chartData.map((item, index) => {
          const xItem = {
            value: index+1 + '',
            textStyle: {
              color: 'rgba(255,255,255,0)',
              // color: '#666'
            }
          }
          if(index === 0 || !((index+1) % 5)) {
            xItem.textStyle.color = '#666'
          }
            xData.push(xItem)
          })

        if(length > 30) {
          xData[29].textStyle.color = 'rgba(255,255,255,0)'
          // xData.splice(29, 1)
          xData[30].textStyle.color = '#666'
          // xData[28].textStyle.color = '#666'
        } else if(length < 30) {
          xData[length-1].textStyle.color = '#666'
        }
        
        xData[length] = {
          value: '',
          textStyle: { color: 'rgba(255,255,255,0)', }
        }
        // console.log(xData);
        
        this.options.xAxis[0].data = xData
      }
    }
  },
  created() {   
    this.setOption()
  },
  mounted() {    
    
    this.myChart = echarts.init(this.$refs.chart)
    this.myChart.on('rendered', () => {
      this.imgUrl = this.myChart.getDataURL({
        type: 'png',
        pixelRatio: 3
      });
      this.$emit('input', this.imgUrl)
      
    })
    this.myChart.setOption(this.options)
  },
  watch: {
    chartData(newData) {      
      this.setOption()
      this.myChart.setOption(this.options)
    }
  }
}
</script>

<style lang="scss" scoped>
/**line-chart */
  .chart-wrap {
    height: 100%;
    width: 100%;
  }
</style>